import React, { useState, useEffect } from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Route,
  Switch,
  Redirect,
} from "react-router-dom";
import WatchlistPage from "./WebPages/WatchlistPage.js";
import MarketPage from "./WebPages/MarketPage";
import AccountPage from "./WebPages/Account/AccountPage";
import StocksPage from "./WebPages/StocksPage";
import RoboTradingPage from "./WebPages/RoboTradingPage";
import TradingPage from "./WebPages/TradingPage/TradingPage";
import NoMatch from "./WebPages/NoMatch";
import PageContainer from "./WebPages/PageContainer";
import socketIOClient from "socket.io-client";
import io from "socket.io-client";
import Login from "./PageElements/Login";
import { connect } from "react-redux";
import { changeLoginDisplay } from "./Redux/LoginState/actions";
import { requestUserData } from "./Redux/UserData/actions";
import LoginPage from "./WebPages/LoginPage/LoginPage";
import PrivateRoute from "./Components/PrivateRoute";
import PublicRoute from "./Components/PublicRoute";

const mapDispatchToProps = (dispatch) => {
  return {
    setShowLogin: (display) => dispatch(changeLoginDisplay(display)),
    getUserData: () => dispatch(requestUserData()),
  };
};

const mapStateToProps = (state) => {
  return {
    showLogin: state.loginState.showLogin,
    userData: state.userDataObj.userData,
    isUserLoggedIn: state.loginStatus.userIsLoggedIn,
  };
};

const ENDPOINT = "http://localhost:8080";
function App({ showLogin, getUserData, userData, isUserLoggedIn }) {
  // const [response, setResponse] = useState(0);

  // console.log("app reloaded");
  // useEffect(() => {
  //   let socket = io(ENDPOINT);

  //   socket.on("FromAPI", (data) => {
  //     socket.emit("server2", "React");
  //     console.log(data);
  //     setResponse(data);
  //   });
  //   return () => socket.disconnect();
  // }, []);

  useEffect(() => {
    if (isUserLoggedIn) {
      getUserData();
    }
  }, []);

  const displayLogin = () => {
    if (showLogin) {
      return (
        <Login
          particles
          style={{
            position: "absolute",
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -50%)",
          }}
        />
      );
    }
  };

  return (
    <div className="App">
      <Router>
        <Switch>
          <PrivateRoute path="/app">
            <PageContainer>
              <Switch>
                <Route exact path="/app">
                  <MarketPage />{" "}
                </Route>
                <Route exact path="/app/watchlist">
                  <WatchlistPage />{" "}
                </Route>
                <Route exact path="/app/account">
                  <AccountPage />{" "}
                </Route>
                <Route exact path="/app/stocks">
                  <StocksPage />{" "}
                </Route>
                <Route exact path="/app/trade">
                  <TradingPage />
                </Route>

                <Route exact path="/app/robo-trade">
                  <RoboTradingPage />
                </Route>
                <Route>
                  <Redirect to="/app" />
                </Route>
              </Switch>
            </PageContainer>
          </PrivateRoute>

          <PublicRoute
            component={LoginPage}
            restricted={true}
            exact
            path="/login"
          />

          <PrivateRoute>
            <Redirect to="/app" />
          </PrivateRoute>
        </Switch>
      </Router>
    </div>
  );
}

export default connect(mapStateToProps, mapDispatchToProps)(App);
