import React, { useState } from "react";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Accordion from "@material-ui/core/Accordion";
import ErrorBoundary from "../Components/ErrorBoundary";
import AccordionSummary from "@material-ui/core/AccordionSummary";
import AccordionDetails from "@material-ui/core/AccordionDetails";
import Typography from "@material-ui/core/Typography";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import MenuItem from "@material-ui/core/MenuItem";
import MenuList from "@material-ui/core/MenuList";
import DeleteOutlineIcon from "@material-ui/icons/DeleteOutline";
import IconButton from "@material-ui/core/IconButton";
import Tooltip from "@material-ui/core/Tooltip";
import TextField from "@material-ui/core/TextField";
import "./OrderEntry.css";
import Menu from "@material-ui/core/Menu";
import classnames from "classnames";
import Popper from "@material-ui/core/Popper";
import ClickAwayListener from "@material-ui/core/ClickAwayListener";
import Grow from "@material-ui/core/Grow";

const OrderEntry = () => {
  const [side, setSide] = useState("Buy");
  const [symbol, setSymbol] = useState("");
  const [type, setType] = useState("");
  const [quantity, setQuantity] = useState(0);
  const [limitPrice, setLimitPrice] = useState(0);
  const [expiration, setExpiration] = useState("");

  const [typeMenuAnchor, setTypeMenuAnchor] = useState(null);
  const [open, setOpen] = useState(false);
  const anchorRef = React.useRef(null);

  const typeMenuOptions = {
    toggle: (event) => setOpen((prevOpen) => !prevOpen),
    close: (event) => {
      if (anchorRef.current && anchorRef.current.contains(event.target)) {
        return;
      }
      setOpen(false);
    },
    select: (event) => setType(event.target.value),
  };

  const typeMenu = () => {
    return (
      <Popper
        style={{ zIndex: "100" }}
        open={open}
        anchorEl={anchorRef.current}
        role={undefined}
        transition
        disablePortal
      >
        {({ TransitionProps, placement }) => (
          <Grow
            {...TransitionProps}
            style={{
              positions: "absolute",
              transformOrigin: "center bottom",
            }}
          >
            <Paper>
              <ClickAwayListener onClickAway={typeMenuOptions.close}>
                <MenuList id="type-menu-list-grow">
                  <MenuItem onClick={typeMenuOptions.close}>Limit</MenuItem>

                  <MenuItem>Market</MenuItem>
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    );
  };

  const expirationMenu = () => {
    return (
      <Popper
        style={{ zIndex: "100" }}
        open={open}
        anchorEl={anchorRef.current}
        role={undefined}
        transition
        disablePortal
      >
        {({ TransitionProps, placement }) => (
          <Grow {...TransitionProps}>
            <Paper
              style={{
                positions: "absolute",
                transformOrigin: "center bottom",
                transform: "100%",
              }}
            >
              <ClickAwayListener onClickAway={typeMenuOptions.close}>
                <MenuList id="type-menu-list-grow">
                  <MenuItem onClick={typeMenuOptions.close}>Limit</MenuItem>

                  <MenuItem>Market</MenuItem>
                </MenuList>
              </ClickAwayListener>
            </Paper>
          </Grow>
        )}
      </Popper>
    );
  };

  return (
    <ErrorBoundary>
      <div style={{ height: "100px", width: "100%" }}>
        <TableContainer component={Paper}>
          <Table size="small" aria-label="customized table">
            <TableHead>
              <TableRow className="tableHeader">
                <TableCell></TableCell>
                <TableCell>Symbol</TableCell>
                <TableCell>Side</TableCell>
                <TableCell>Quantity</TableCell>
                <TableCell>Type</TableCell>
                <TableCell>Limit Price</TableCell>
                <TableCell>Expiration</TableCell>
              </TableRow>
            </TableHead>{" "}
            <TableBody>
              <TableRow
                className={classnames("orderRow ", {
                  rowColorRed: side === "Sell",
                  rowColorGreen: side === "Buy",
                })}
              >
                <TableCell>
                  <Tooltip title="Delete">
                    <IconButton size="small" className="deleteButton">
                      <DeleteOutlineIcon fontSize="small" />
                    </IconButton>
                  </Tooltip>
                </TableCell>
                <TableCell>
                  <div>{symbol}</div>
                </TableCell>
                <TableCell>
                  <div
                    className="pointer"
                    onClick={() =>
                      setSide((prevSide) =>
                        prevSide === "Buy" ? "Sell" : "Buy"
                      )
                    }
                  >
                    {side}
                  </div>
                </TableCell>
                <TableCell>
                  {" "}
                  <TextField
                    id="order-quantity-entry"
                    type="number"
                    defaultValue={quantity}
                    InputLabelProps={{
                      shrink: true,
                    }}
                  />
                </TableCell>
                <TableCell>
                  <Accordion className="orderAccordion" ref={anchorRef}>
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="order-expiration-entry"
                      id="order-type-entry"
                    >
                      <Typography>Limit</Typography>
                    </AccordionSummary>
                  </Accordion>
                </TableCell>
                <TableCell>
                  {" "}
                  <TextField
                    id="order-limit-price-entry"
                    type="number"
                    InputLabelProps={{
                      shrink: true,
                    }}
                    defaultValue={limitPrice}
                  />
                </TableCell>
                <TableCell>
                  <Accordion
                    ref={anchorRef}
                    onClick={typeMenuOptions.toggle}
                    className="orderAccordion"
                  >
                    <AccordionSummary
                      expandIcon={<ExpandMoreIcon />}
                      aria-controls="order-expiration-entry"
                      id="order-expiration-entry"
                    >
                      <Typography>Day</Typography>
                    </AccordionSummary>
                  </Accordion>
                </TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </TableContainer>
        <div
          style={{
            display: "flex",
            marginTop: "6px",
            justifyContent: "space-between",
            wrap: "nowrap",
            fontSize: "1rem",
          }}
        >
          <div>
            <Typography variant="subtitle2"> Add Order</Typography>
          </div>
          <div>
            <Typography variant="subtitle2">
              Amount(est.):{quantity * limitPrice}
            </Typography>
          </div>
          <div>
            <Typography variant="subtitle2">Buying Power:</Typography>
          </div>
          <div>
            <Typography variant="subtitle2">Max Shares to Buy:</Typography>
          </div>
          <div>
            <Button style={{ backgroundColor: "gold" }}>Place New Order</Button>
          </div>
        </div>
        {typeMenu()}
      </div>
    </ErrorBoundary>
  );
};

export default OrderEntry;
