import React, { useState, useEffect } from "react";
import CustomTab from "../CustomTab";
import OrderTable from "./OrderTable";
import { isArrayValid } from "../../Essential";

const OrdersTab = () => {
  const [workingOrders, setWorkingOrders] = useState([]);
  const [filledOrders, setFilledOrders] = useState([]);
  const [cancelledOrders, setCancelledOrders] = useState([]);
  const [terminatedOrders, setTerminatedOrders] = useState([]);
  const [allOrders, setAllOrders] = useState([]);

  useEffect(() => {
    const orders = [
      {
        _id: "5fbef71be83034433c7fa671",
        quantity: 123,
        startTime: "2020-11-25T22:47:11.312Z",
        status: "WORKING",
        limitPrice: 0,
        quantityFilled: 0,
        executionPrice: 0,
        side: "BUY",
        symbol: "ABBV",
        orderType: "MARKET",
        expireType: "GTC",
        username: "einstein",
      },
    ];
    if (isArrayValid(orders)) {
      setAllOrders(orders);

      const working = [];
      const filled = [];
      const terminated = [];
      const cancelled = [];

      orders.filter((order, i) => {
        switch (order.status) {
          case "WORKING":
            working.push(order);
            break;
          case "CANCELLED":
            cancelled.push(order);
            break;
          case "FILLED":
            filled.push(order);
            break;
          case "TERMINATED":
            terminated.push(order);
            break;
          default:
            break;
        }
      });

      setWorkingOrders(working);
      setFilledOrders(filled);
      setCancelledOrders(cancelled);
      setTerminatedOrders(terminated);
    }
  }, []);

  return (
    <div>
      <CustomTab tabs={["Working", "Filled", "Cancelled", "Terminated", "All"]}>
        {[
          <OrderTable className="working-orders" orders={workingOrders} />,
          <OrderTable className="filled-orders" orders={filledOrders} />,
          <OrderTable className="cancelled-orders" orders={cancelledOrders} />,
          <OrderTable
            className="terminated-orders"
            orders={terminatedOrders}
          />,
          <OrderTable className="terminated-orders" orders={allOrders} />,
        ]}
      </CustomTab>
    </div>
  );
};

export default OrdersTab;
